            const F1 = 0.5 * (Math.sqrt(3) - 1)     //
            const F2 = (3 - Math.sqrt(3)) / 6       //

            function grad(f, x, y) {
                    switch (f & 7) {
                        case 0: return y
                        case 1: return x + y
                        case 2: return x
                        case 3: return x - y
                        case 4: return - y
                        case 5: return - x - y
                        case 6: return - x + y
                        case 7: return - x
                    }
            }

            function simplex(x, y) {
                var x_y = (x + y) * F1
                var sx = (x + x_y) | 0
                var sy = (y + x_y) | 0
                var sx_xy = (sx + sy) * F2
                var offsetx = x - (sx - sx_xy)
                var offsety = y - (sy - sx_xy)
                var hash1 = sx & 255
                var hash2 = sy & 255
                var zdwyl1 = hashtable[hash2]
                var zdwyl2 = hashtable[hash2 + 1]

                if (offsetx > offsety) {
                    var x1 = offsetx - 1 + F2
                    var y1 = offsety + F2
                    var x2 = offsetx - 1 + 2 * F2
                    var y2 = offsety - 1 + 2 * F2

                    var f1 = hashtable[hash1 + zdwyl1]
                    var f2 = hashtable[hash1 + 1 + zdwyl1]
                    var f3 = hashtable[hash1 + 1 + zdwyl2]
                } else {
                    var x1 = offsetx + F2
                    var y1 = offsety - 1 + F2
                    var x2 = offsetx - 1 + 2 * F2
                    var y2 = offsety - 1 + 2 * F2

                    var f1 = hashtable[hash1 + zdwyl1]
                    var f2 = hashtable[hash1 + zdwyl2]
                    var f3 = hashtable[hash1 + 1 + zdwyl2]
                }

                


                var o1 = 0.5 - offsetx * offsetx - offsety * offsety
                var o2 = 0.5 - x1 * x1 - y1 * y1
                var o3 = 0.5 - x2 * x2 - y2 * y2

                var len1 = (o1 >= 0 ? o1 : 0)
                var len2 = (o2 >= 0 ? o2 : 0)
                var len3 = (o3 >= 0 ? o3 : 0)

                var n1 = len1 * len1 * len1 * len1 * grad(f1, offsetx, offsety)
                var n2 = len2 * len2 * len2 * len2 * grad(f2, x1, y1)
                var n3 = len3 * len3 * len3 * len3 * grad(f3, x2, y2)

