            function vv() {
                that.vvinternal = window.requestAnimationFrame(vv)
                ttt++
                var tt = Math.sin(ttt*0.005)
                tt = tt * tt * 30 + 40
                for (let x = 0; x < simcanvas2.width; x+=1) {
                    for (let y = 0; y < simcanvas2.height; y+=1) {
                        var c = (x + y * simcanvas2.width) * 4;

                        var va = Math.abs(140*simplex(x*0.005,y*0.005) * (tt *0.01)+100*simplex(x*0.025,y*0.025) * (tt *0.003))
                        var vb = va*0.01
                        if (vb < 0.2) {
                            data[c] = 234
                            data[c + 1] = 190
                            data[c + 2] = 22+vb*260
                            data[c + 3] = (vb+0.2)*5*255
                        } else if (vb<0.4) {
                            data[c] = 255
                            data[c + 1] = 155
                            data[c + 2] = 0+vb*100
                            data[c + 3] = (vb+0.2)*166+155
                        } else if (vb<0.6) {
                            data[c] = 255
                            data[c + 1] = 109
                            data[c + 2] = 0+vb*26
                            data[c + 3] = (vb+0.2)*125+155
                        } else if (vb<0.8) {
                            data[c] = 255
                            data[c + 1] = 63
                            data[c + 2] = 0+vb*10
                            data[c + 3] = (vb+0.1)*255
                        } else {
                            data[c] = 255
                            data[c + 1] = 30
                            data[c + 2] = 0+vb*10
                            data[c + 3] = (vb+0.2)*255
                        }

                    }
                }
                simctx2.putImageData(image, 0, 0)
            }

            var vvover = new IntersectionObserver(
