        letter-spacing: 1px;
        text-indent: 2em;
        margin: 40px 0;
        font-size: 1em;
    }
    .snoiseimg{
        display: flex;
        justify-content: center;
    }
    .snoiseimg img{
        width: 80%;
    }
    .tons_code{
        width: 100%;
        box-sizing: border-box;
        font-size: 13px !important;
        margin: 30px 0 80px 0;
        display: flex;
        overflow: overlay;
        justify-content: start;
        background-color: #282C34;
        max-height: 600px;
}
    .tons_code::-webkit-scrollbar{
        width: 5px;
        height: 5px;
    }
    .tons_code::-webkit-scrollbar-corner{
        background-color: transparent;
    }
    .tons_code::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: #b6b6b6;
}
    .tons_code *{
        font-family: 'Roboto Mono', monospace;
    }
