                <p>nx&788</p>
                <div class="tons_code">
                    <pre v-highlight><code>
            function getgradients(hash,x,y){
                switch(hash & 7){
                    case 0: return y;
                    case 1: return x + y;
                    case 2: return x;
                    case 3: return x - y;
                    case 4: return - y;
                    case 5: return - x - y;
                    case 6: return - x;
                    case 7: return - x + y;}
            }      
                    </code></pre>
