                    context8.stroke()



                    // context8.beginPath()
                    // fec = context8.createLinearGradient(0,0,canvas8.width,0);
                    // context8.moveTo(-10,x)
                    // for(let y=0;y<canvas8.height+1;y+=10){
                    //     fec.addColorStop(y/canvas8.height, "rgb(91, 212, 176,"+Math.abs(jnjn[x/10*(canvas8.width/10+1)+y/10]/30)+")");
                    //     context8.lineTo(y,x+jnjn[x/10*(canvas8.height/10+1)+y/10])
                    // }
                    // context8.strokeStyle= fec
                    // // context8.stroke()

                }
            }
            lines()
            

            // var obs = new IntersectionObserver(
            //     function(e){
            //         let _e = e[0]
            //         if(_e.isIntersecting) window.requestAnimationFrame(lines)
            //         else window.cancelAnimationFrame(intrr)
            //     },
            //     {
            //         thresholds:0.01
            //     }
            // )
            // obs.observe(document.getElementById('canvas8'))



            var obss = new IntersectionObserver(
                function(e){
                    let _e = e[0]
                    if(_e.isIntersecting) window.requestAnimationFrame(thiss.huge)
                    else window.cancelAnimationFrame(thiss.innn)
                },
                {
                    thresholds:0.01
                }
            )
            obss.observe(document.getElementById('canvas6'))









        },
        watch:{
            strange_m(){
                this.huge()
            },
            strange_n(){
                this.huge()
            },
            strange_h(){
                this.huge()
            },
            strange_t(){
                this.huge()
            },
            strange_p(){
                this.huge()
            },
            sbstate(){
                this.huge()
            }
        },
    methods: {
        huge() {
            
            const that=this 
            // canvas-----6
            var canvas6=document.getElementById('canvas6')
            var context6=canvas6.getContext('2d');

            let array=[]
            var anglegap=this.strange_m   //
            var anpace=360/anglegap
            for(let n=0;n<anglegap;n++){
                array.push(Math.random()*40-20)   //
            }
            function noise(x,pace,array){
                let ratio=x/pace
                let z=ratio|0
                let dx=ratio-z
                let dy=dx - 1
                let t = 6 * dx * dx * dx * dx * dx - 15 * dx * dx * dx * dx + 10 * dx * dx * dx
                let xx=dx*array[z]
                let yy=dy*(array[z+1] !=undefined ? array[z+1] : array[0])
                return xx+t*(yy-xx)
            }

            let afc=[]
            var dll=this.strange_n  //
            var sll=360/dll
            for(let n=0;n<dll;n++){
                afc.push(Math.random()*this.strange_t-this.strange_t/2) //
            }
            let evve=0


            var speedd=this.strange_p/10 //

            function drrrr(){
                that.innn = window.requestAnimationFrame(drrrr)
                context6.clearRect(0, 0, canvas6.width, canvas6.height);
                let ecc=[]
                for(let an=0;an<=Math.PI*2;an+=that.strange_h){  //
                    let length = 100+noise(an*180/Math.PI,anpace,array)
                    ecc.push({x:length*Math.cos(an),y:length*Math.sin(an)})
                
                }
                ecc.push({x:100,y:0})
                context6.beginPath()
                ecc.forEach((item,index)=>{
                    if(index===0){
                        context6.moveTo(400+item.x,200+item.y)
                    }else{
                        context6.lineTo(400+item.x,200+item.y)
                    }
                })
                if(!that.sbstate){
                    context6.strokeStyle='#5bd4b0';
                    context6.stroke()
                }else{
                    context6.fillStyle='#5bd4b0';
                    context6.fill()
                }

                evve+=speedd
                for(let x=0;x<anglegap;x++){
                    let step=360/anglegap
                    let new_evve=(evve+step*x) % 360 
                    array[x]+=noise(new_evve,sll,afc)*40  //
                }
            }
                window.requestAnimationFrame(drrrr)
            
            },
            dnoise(valuex,valuey){
                const table=[151,160,137,91,90,15,131,13,201,95,96,53,194,233,7,225,140,36,103,30,69,142,8,99,37,240,21,10,23,190,6,148,247,120,234,75,0,26,
                            197,62,94,252,219,203,117,35,11,32,57,177,33,88,237,149,56,87,174,20,125,136,171,168, 68,175,74,165,71,134,139,48,27,166,77,146,158,
                            231,83,111,229,122,60,211,133,230,220,105,92,41,55,46,245,40,244,102,143,54,65,25,63,161,1,216,80,73,209,76,132,187,208,89,18,169,
                            200,196,135,130,116,188,159,86,164,100,109,198,173,186,3,64,52,217,226,250,124,123,5,202,38,147,118,126,255,82,85,212,207,206,59,227,
                            47,16,58,17,182,189,28,42,223,183,170,213,119,248,152,2,44,154,163,70,221,153,101,155,167,43,172,9,129,22,39,253,19,98,108,110,79,113,
                            224,232,178,185,112,104,218,246,97,228,251,34,242,193,238,210,144,12,191,179,162,241,81,51,145,235,249,14,239,107,49,192,214,31,181,
                            199,106,157,184,84,204,176,115,121,50,45,127,4,150,254,138,236,205,93,222,114,67,29,24,72,243,141,128,195,78,66,215,61,156,180,

                            151,160,137,91,90,15,131,13,201,95,96,53,194,233,7,225,140,36,103,30,69,142,8,99,37,240,21,10,23,190,6,148,247,120,234,75,0,26,
                            197,62,94,252,219,203,117,35,11,32,57,177,33,88,237,149,56,87,174,20,125,136,171,168, 68,175,74,165,71,134,139,48,27,166,77,146,158,
                            231,83,111,229,122,60,211,133,230,220,105,92,41,55,46,245,40,244,102,143,54,65,25,63,161,1,216,80,73,209,76,132,187,208,89,18,169,
                            200,196,135,130,116,188,159,86,164,100,109,198,173,186,3,64,52,217,226,250,124,123,5,202,38,147,118,126,255,82,85,212,207,206,59,227,
                            47,16,58,17,182,189,28,42,223,183,170,213,119,248,152,2,44,154,163,70,221,153,101,155,167,43,172,9,129,22,39,253,19,98,108,110,79,113,
                            224,232,178,185,112,104,218,246,97,228,251,34,242,193,238,210,144,12,191,179,162,241,81,51,145,235,249,14,239,107,49,192,214,31,181,
                            199,106,157,184,84,204,176,115,121,50,45,127,4,150,254,138,236,205,93,222,114,67,29,24,72,243,141,128,195,78,66,215,61,156,180]
                function iplt(dx,x,y){
                    let t = 6 * dx * dx * dx * dx * dx - 15 * dx * dx * dx * dx + 10 * dx * dx * dx
                    return x+t*(y-x)
                }

                function getgradients(hash,x,y){
                    switch(hash & 7){
                        case 0: return y
                        case 1: return x + y
                        case 2: return x
                        case 3: return x - y
                        case 4: return - y
                        case 5: return - x - y
                        case 6: return - x
                        case 7: return - x + y
                    }
                    
                }

                function tdnoise(x,y){
                    let bx = x - (x | 0)
                    let by = y - (y | 0)
                    
                    let floorx = x & 255
                    let floory = y & 255
                    
                    let n1 = table[floorx + table[floory]]
                    let n2 = table[floorx + 1 + table[floory]]
                    let n3 = table[floorx + table[floory + 1]]
                    let n4 = table[floorx + 1 + table[floory + 1]]

                    let m1 = getgradients(n1,bx,by)
                    let m2 = getgradients(n2,bx-1,by)
                    let m3 = getgradients(n3,bx,by-1)
                    let m4 = getgradients(n4,bx-1,by-1)

                    let r1 = iplt(bx,m1,m2)
                    let r2 = iplt(bx,m3,m4)

                    return iplt(by,r1,r2)
                }

                return tdnoise(valuex,valuey)
            },
            color(){
                let canvas7=document.getElementById('canvas7')
                let context7=canvas7.getContext('2d');


                



                var image = context7.createImageData(canvas7.width, canvas7.height);
                var data = image.data;

                for(let x = 0;x<canvas7.width;x+=1){
                    for(let y = 0;y<canvas7.height;y+=1){
                            var c = (x + y * canvas7.width) * 4;
                            data[c] = 91 
                            data[c + 1] = 212
                            data[c + 2] = 176
                            data[c + 3] = Math.max(0,Math.abs(this.dnoise(x/100,y/100)*300))
                    }
                }

                context7.putImageData(image, 0, 0)

                
                



                                    
            },
            

        }
    }
</script>

